import React from 'react';

interface TEProps {
  title?: string;
  message: string;
  visible?: boolean;
}

export default function TaskEmpty<FC>(props: TEProps) {
  const { title, message, visible = true } = props;
  const isShown = visible? true : null;
  return (isShown &&
    <>
      {title && <div className="task-title">{title}</div>}
      <div className="empty-page">
        <h2>{message}</h2>
      </div>
    </>
  );
}
