import React from 'react';

interface BProps{
	addTask: () => void;
	visible: boolean;
}

export default function TaskAddButton(props: BProps ){
	const {addTask, visible} = props;
	const isShown = visible? true : null;

	return (isShown &&
			<div className="task-add-button" onClick={addTask}>
        +
      </div>
		);
}